
import { CartProvider, useCart } from "react-use-cart";
import {Col, Container, Row } from "reactstrap";

function Page() {
  const { addItem } = useCart();

  const products = [
    {
      id: 1,
      title: "Web Designing",
      description: "Stella Blair.",
      price: 160,
      images: [
        "/assets/images/course-01.jpg",
        "https://cdn.dummyjson.com/product-images/1/2.jpg",
        "https://cdn.dummyjson.com/product-images/1/3.jpg",
        "https://cdn.dummyjson.com/product-images/1/4.jpg",
        "https://cdn.dummyjson.com/product-images/1/thumbnail.jpg",
      ],
    },
    {
      id: 2,
      title: "Web Development",
      description: "Cindy Walker.",
      price: 340,
      images: [
        "/assets/images/course-02.jpg",
        "https://cdn.dummyjson.com/product-images/2/2.jpg",
        "https://cdn.dummyjson.com/product-images/2/3.jpg",
        "https://cdn.dummyjson.com/product-images/2/thumbnail.jpg",
      ],
    },
    {
      id: 3,
      title: "Wordpress Course",
      description: "David Huston.",
      price: 640,
      images: ["/assets/images/course-03.jpg"],
    },
    {
      id: 4,
      title: "Full Stack Course",
      description: "Stella Blair.",
      price: 240,
      images: [
        "/assets/images/course-06.jpg",
        "https://cdn.dummyjson.com/product-images/4/2.jpg",
        "https://cdn.dummyjson.com/product-images/4/3.jpg",
        "https://cdn.dummyjson.com/product-images/4/4.jpg",
        "https://cdn.dummyjson.com/product-images/4/thumbnail.jpg",
      ],
    },
    {
      id: 5,
      title: "Data Analysis",
      description: "David Huston.",
      price: 320,
      images: [
        "/assets/images/course-04.jpg",
        "https://cdn.dummyjson.com/product-images/5/2.jpg",
        "https://cdn.dummyjson.com/product-images/5/3.jpg",
      ],
    },
    {
      id: 6,
      title: "UI/UX Course",
      description: "David Huston.",
      price: 350,
      images: [
        "/assets/images/course-05.jpg",
        "https://cdn.dummyjson.com/product-images/6/2.jpg",
        "https://cdn.dummyjson.com/product-images/6/3.png",
        "https://cdn.dummyjson.com/product-images/6/4.jpg",
      ],
    },
  ];

  return (
    <div className="section courses" id="courses">
      <Container>
        <Row>
          <Col lg="12" className="text-center">
            <div className="section-heading">
              <h6>Latest Courses</h6>
              <h2>Latest Courses</h2>
            </div>
          </Col>
          <ul className="event_filter">
            <li>
              <a className="is_active" href="#!" data-filter="*">
                Show All
              </a>
            </li>
            <li>
              <a href="#!" data-filter=".design">
                Webdesign
              </a>
            </li>
            <li>
              <a href="#!" data-filter=".development">
                Development
              </a>
            </li>
            <li>
              <a href="#!" data-filter=".wordpress">
                Wordpress
              </a>
            </li>
          </ul>
        </Row>
        <Row className="event_box">
          {products.map((item) => (
            <Col
              lg="4"
              md="6"
              className="align-self-center mb-30 event_outer design"
              key={item.id}
            >
              <div class="events_item">
                <div class="thumb">
                  <a href="#">
                    <img src={item.images[0]} alt="" />
                  </a>

                  <span class="price">
                    <h6>{item.price}$</h6>
                  </span>
                </div>
                <div class="down-content">
                  <span class="author">{item.description}</span>
                  <h4>{item.title}</h4>
                </div>
                <button
                  className="btn btn-success mx-5 mb-3 w-75"
                  onClick={() => addItem(item)}
                >
                  Add to cart
                </button>
              </div>
            </Col>
          ))}
        </Row>
      </Container>
    </div>
  );
}



function AllCourses() {
  return (
    <CartProvider>
      <Page />
    </CartProvider>
  );
}
export default AllCourses;
